.component {
    padding: 5px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: #fff;
    border-bottom: 1px solid #f5f5f5;
  }
  
  .wrapper {
    white-space: nowrap;
    overflow: hidden;
  }